import {
  Worker,
  Viewer,
  SpecialZoomLevel,
  TextDirection,
  LocalizationMap,
} from "@react-pdf-viewer/core";
import PropTypes from "prop-types";
import React, { useState } from "react";
import "@react-pdf-viewer/core/lib/styles/index.css";

import ar_AE from "@react-pdf-viewer/locales/lib/ar_AE.json";
import fr_FR from "@react-pdf-viewer/locales/lib/fr_FR.json";
import en_US from "@react-pdf-viewer/locales/lib/en_US.json";
import { zoomPlugin } from "@react-pdf-viewer/zoom";

function PDFPage(props) {
  const langue = localStorage.getItem("language");
  const { pdf } = props;
  const [url, seturl] = useState(pdf);
  const zoomPluginInstance = zoomPlugin();

  const { Zoom } = zoomPluginInstance;
  return (
    <div style={{ maxHeight: "500px", overflowY: "scroll" }}>
      {url ? (
        <div
          style={{
            border: "1px solid rgba(0, 0, 0, 0.3)",
            height: "100%",
          }}
        >
          <div
            style={{
              display: "flex",
              borderBottom: "1px solid rgba(0, 0, 0, .3)",
              padding: "0.25rem 0",
              justifyContent: "center",
            }}
          >
            <Zoom levels={[0.4, 0.8, 1.2, 1.6, 2.4, 3.2]} />
          </div>
          <Viewer
            fileUrl={url}
            localization={
              langue === "fr"
                ? (fr_FR as unknown as LocalizationMap)
                : langue === "ar"
                ? (ar_AE as unknown as LocalizationMap)
                : (en_US as unknown as LocalizationMap)
            }
            theme={{
              direction:
                langue === "ar"
                  ? TextDirection.RightToLeft
                  : TextDirection.LeftToRight,
            }}
            plugins={[zoomPluginInstance]}
          />
        </div>
      ) : (
        <div
          style={{
            alignItems: "center",
            border: "2px dashed rgba(0, 0, 0, .3)",
            display: "flex",
            fontSize: "2rem",
            height: "100%",
            justifyContent: "center",
            width: "100%",
          }}
        >
          Not pdf Found
        </div>
      )}
    </div>
  );
}
PDFPage.propTypes = {
  pdf: PropTypes.string,
};
export default PDFPage;
